import React, { useContext } from 'react'
import { Text, LinkExternal, Modal } from 'archerswap-uikit'
import copy from 'copy-to-clipboard'
import styled, { ThemeContext } from 'styled-components'
import { isMobile } from 'react-device-detect'
import { useTranslation } from 'contexts/Localization'
import { CheckCircle, Copy, Upload } from 'react-feather'
import { TokenCreationContext } from 'contexts/TokenCreationContext'
// import Modal from 'components/Modal'
import { getSnowtraceLink } from '../../utils'

const Wrapper = styled.div`
  width: 500px;
  padding: 10px;
  @media (max-width: 768px) {
    width: 100%;
  }
`

const TokenInfo = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 20px;
  .right {
    margin-left: 15px;
  }
  .flex-row {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
`

interface ModalProps {
  onDismiss?: () => void
}

const TokenCreationModal: React.FC<ModalProps> = ({ onDismiss }) => {
  const { t } = useTranslation()
  const theme = useContext(ThemeContext)
  const { tokenAddr, isVerified } = useContext(TokenCreationContext)

  // const handleDismiss = useCallback(() => null, [])
  return (
    <Modal title={t('Token Created Successfully')} onDismiss={onDismiss}>
      <Wrapper>
        <TokenInfo>
          <CheckCircle strokeWidth={0.5} size={30} color={theme.colors.primary} />
          <div className="right">
            <Text>Token Address</Text>
            <div className="flex-row">
              <Text style={{ marginRight: '10px' }}>
                {isMobile ? `${tokenAddr.substring(0, 15)}...${tokenAddr.substring(30)}` : tokenAddr}
              </Text>
              <Copy
                style={{ cursor: 'pointer' }}
                strokeWidth={0.5}
                size={20}
                color={theme.colors.primary}
                onClick={() => copy(tokenAddr)}
              />
            </div>
          </div>
        </TokenInfo>
        <TokenInfo>
          <CheckCircle strokeWidth={0.5} size={30} color={theme.colors.primary} />
          <div className="right">
            <Text>Token Link</Text>
            <LinkExternal href={getSnowtraceLink(1116, tokenAddr, 'token')}>View on Core Scan</LinkExternal>
          </div>
        </TokenInfo>
        <TokenInfo>
          {isVerified ? (
            <CheckCircle strokeWidth={0.5} size={30} color={theme.colors.primary} />
          ) : (
            <Upload strokeWidth={0.5} size={30} color={theme.colors.primary} />
          )}
          <div className="right">
            <Text>Verification Status</Text>
            <Text>{isVerified ? 'Verified Successfully' : 'Trying to verify the contract...'}</Text>
          </div>
        </TokenInfo>
      </Wrapper>
    </Modal>
  )
}

export default TokenCreationModal
